.full-width {
  width: 100%;
  text-align: center;
}

.blur {
  filter: blur(3px);
  transition: filter .1s ease-in-out;
}

.text-underline {
  text-decoration: underline;
}

.text-red {
  color: $red;
}

.text-green {
  color: $green;
}