// Slow.trade particle logo animation
$logoWidth: 78px;
$logoHeight: 150px;

$logoTextHeight: 42px;
$logoTextWidth: 260px;

.logo {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;

  @media #{$tablet} { 
    justify-content: center;
  }

  @media #{$mobile} { 
    justify-content: center;
  }
}

.logoText {
  height: $logoTextHeight;
  width: $logoTextWidth;

  @media #{$mobile} {  
    height: 25px;
    width: 170px;
  }
}

.slowTradeSpinnerWrap {
  width: $logoWidth;
  height: $logoHeight;
  margin: 0 25px 0 0;

  @media #{$tablet} {  
    margin: 0 0 25px 0;
    flex-basis: 100%;
  }

  @media #{$mobile} {  
    margin: 0 0 25px 0;
    flex-basis: 100%;
    // width: ($logoWidth / 2);
    // height: ($logoHeight / 2);
  }

  > p {
    margin-top: 45px;
  }
}

.slowTradeSpinner {
  width: $logoWidth;
  height: $logoHeight;
  background: transparent;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  backface-visibility: hidden;
  transform: translateZ(0);
  perspective: 1000;
  will-change: transform;
  margin: auto;
  z-index: 100;

  // @media #{$mobile} {  
  //   width: ($logoWidth / 2);
  //   height: ($logoHeight / 2);
  // }

  > span {
    width: ($logoWidth / 2);
    height: ($logoHeight / 2);
    margin: 0;
    will-change: translate3d;
    opacity: 0;

    // @media #{$mobile} { 
    //   width: ($logoWidth / 4);
    //   height: ($logoHeight / 4);
    // }
  }

  > span:nth-of-type(1) {
    background: url('#{$images}logo_1.svg') no-repeat center / contain;
    animation: initLogo1 1s cubic-bezier(1, 0, 0, 1) .2s 1 normal forwards running, progressLogo1 5s cubic-bezier(1, 0, 0, 1) 1.5s infinite normal forwards running;
  }

  > span:nth-of-type(2) {
    background: url('#{$images}logo_2.svg') no-repeat center / contain;
    animation: initLogo2 1s cubic-bezier(1, 0, 0, 1) .2s 1 normal forwards running, progressLogo2 5s cubic-bezier(1, 0, 0, 1) 1.5s infinite normal forwards running;
  }

  > span:nth-of-type(3) {
    background: url('#{$images}logo_3.svg') no-repeat center / contain;
    animation: initLogo3 1s cubic-bezier(1, 0, 0, 1) .2s 1 normal forwards running, progressLogo3 5s cubic-bezier(1, 0, 0, 1) 1.5s infinite normal forwards running;
  }

  > span:nth-of-type(4) {
    background: url('#{$images}logo_4.svg') no-repeat center / contain;
    animation: initLogo4 1s cubic-bezier(1, 0, 0, 1) .2s 1 normal forwards running, progressLogo4 5s cubic-bezier(1, 0, 0, 1) 1.5s infinite normal forwards running;
  }
}
