// File paths
$images: "../images/";
$fonts: "../fonts/";

// Media Query Ranges
$tiny-screen: 340px !default;
// --------------------------
$small-screen: 736px !default;
$small-screen-up: 737px !default;
$medium-end: 1024px !default;
// --------------------------
$desktop-screen: 1025px !default;
// --------------------------
// --------------------------
$tiny-and-down: "only screen and (max-width : #{$tiny-screen})" !default;
$mobile: "only screen and (max-width : #{$small-screen})" !default;
$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$desktop: "only screen and (min-width : #{$desktop-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-end})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-end})" !default;
$medium-smallresolutions: 850px !default;

// Tablet orientations
$tablet-portrait: "(min-device-width: #{$small-screen-up}) and (max-device-width: #{$medium-end}) and (orientation: portrait)" !default;
$tablet-landscape: "(min-device-width: #{$small-screen-up}) and (max-device-width: #{$medium-end}) and (orientation: landscape)" !default;
$tablet: "(min-width: #{$small-screen-up}) and (max-width: #{$medium-end}), #{$tablet-portrait}, #{$tablet-landscape}";
$tablet-no-portrait: "(min-width: #{$small-screen-up}) and (max-width: #{$medium-end}), #{$tablet-landscape}";
$tablet-small: "(min-width: #{$small-screen-up}) and (max-width: #{$medium-smallresolutions})";
// $ipad-pro-portrait-breakpoint: "(min-device-width: 1024px) and (max-device-height: 1366px) and (orientation:portrait) and (-webkit-min-device-pixel-ratio: 2)";
// $ipad-pro-landscape-breakpoint: "(min-device-width: 1024px) and (max-device-height: 1366px) and (orientation:landscape) and (-webkit-min-device-pixel-ratio: 2)";

// classes
$page-hero: 'html.hasHero';

// Global HEX Colors
$green: #23d160;
$white: #FFF;
$black: #333;
$black-shade: #526179;;
// $grey: #7c7c8c;
$grey: #bbb;
$greyLight: #eee;
$blue: #0072cf;
$red: #bf1600;
$orange: #ed5a00;
$lila: #735ab7;
$purple: #795ba6;
$pink: #ef3e6d;
$gold: #cfac87;

$link: $blue;

$max-width: 960px;
$z-index-max: 1000001;

// Fonts
$helvetica: "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
$spacemono: "SpaceMono", Arial, sans-serif;
$mono: "Courier New", Courier, monospace;
$lucida: 'Lucida Sans', sans-serif;
$arial: Arial, sans-serif;
$verdana: Verdana, Geneva, sans-serif;
$georgia: Georgia, serif;
$defaultFont: $verdana;

// Font weight
$light: 300;
$normal: 400;
$bold: 500;

