@import "core/_variables";
@import "core/_base";
@import "core/_animations";
@import "core/_fonts";

#root {
  margin: 0 auto;
  flex-direction: row;
  display: flex;
  margin: 0 auto;
  max-width: 1024px;
  width: 100%;
  box-sizing: border-box;
  font-size: 1.3em;
  height: 100vh;
  padding: 0 25px;
  align-items: center;
  justify-content: space-between;

  > div {
    flex: 1;
    flex-basis: 50%;

    @media #{$mobile} { 
      margin: 50px auto 0;

        &:last-of-type {
          margin-bottom: 50px;
        }
    }
  }

  > div > h1 {
    color: $blue;
    font-weight: $bold;
    font-size: 2.7em;
    letter-spacing: -1px;
    line-height: 1;

    @media #{$mobile} {  
      text-align: center;
    }
  }

  > div > p {
    @media #{$mobile} {  
      text-align: center;
    }
  }

  @media #{$mobile} { 
    flex-direction: column;
  }
}

@import "components/_logo";