@keyframes BG {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes initLogo1 {
  0% {
    transform: translate3d(-26px,-50px,0);
    opacity: 0;
  }

  30% {
    opacity: .4;
  }

  100% {
    transform: translate3d(0,0,0);
    opacity: 1;
  }
}

