body {
  background: linear-gradient(-45deg, #EAEEF3, #e0e4e8, #FFFFFF, #FFFFFF);
  background-size: 400% 400%;
  animation: BG 15s ease infinite;
  color: $black-shade;
  font-family: $defaultFont;
  font-size: 10px;
  line-height: 1.5;
  margin: 0;
  padding: 0;
  text-rendering: geometricPrecision;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100wh;
	height: 100vh;

  @media #{$tablet} {
  }

  @media #{$mobile} {
  }
}

h1,
h2,
h3 {
  color: $black-shade;
  font-family: $spacemono;
  font-weight: normal;
  margin: 0;
  padding: 0;
}

a:link,
a:visited,
a:active,
a:focus {
  color: $link;
  text-decoration: underline;
}

p {
  white-space: normal; 
  text-align: left;
  line-height: 1.5;
  font-size: 1.3em;
  letter-spacing: 0;
}

::selection {
    color: $white;
    background: $blue;
}

select {
  font-family: inherit;
  outline: 0;
}

button {
  outline: 0;
  border: 0;
  font-family: inherit;
}

nav,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  border: 0;
  -ms-interpolation-mode: bicubic;
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
}

// Prevents img without src to appear
img:not([src]) {
    visibility: hidden;
}

footer {
  margin: 0;
  padding: 0;
}

.noScroll {
  overflow: hidden;
  position: fixed;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

svg:not(:root) {
  overflow: hidden;
}

@import "core/_helper";
@import "components/_button";